export const now = () => Math.floor(Date.now() / 1000);
export const sleep = (ms = 1000) => new Promise(res => setTimeout(res, ms));
export function create_timeout(promise, timeout) {
    return new Promise(async (resolve, reject) => {
        const timer = setTimeout(() => reject('timeout'), timeout);
        const res = await promise;
        clearTimeout(timer);
        resolve(res);
    });
}
export function sort_obj(obj) {
    if (obj instanceof Map || Array.isArray(obj) || typeof obj !== 'object') {
        return obj;
    }
    else {
        return Object.keys(obj)
            .sort()
            .filter(([_, value]) => value !== undefined)
            .reduce((sorted, key) => {
            sorted[key] = obj[key];
            return sorted;
        }, {});
    }
}
export function parse_error(err) {
    if (err instanceof Error)
        return err.message;
    if (typeof err === 'string')
        return err;
    return String(err);
}
